.carousel-extended #IndicateCarousel {
  position: absolute;
  bottom: -50px;
  height: 35px;
  max-width: 70%;
  padding: 10px 10px;
  background-color: #565656;
  border-radius: 15px;
  border-left: 3px silver double;
  border-right: 3px silver double;
}

.carousel-extended .item div > a > img {
  width: 95%;
  min-height: 180px;
  height: 100%;
  margin: auto;
  background: url("../Images/ajax-loader.gif") no-repeat;
  background-position: center;
}

.carousel-extended .itemcaption {
  color: #1f1f1f;
  text-align: center;
}

.carousel-extended .itemcaption h3 {
  font-size: 0.7em;
  display: inline-block;
}

.carousel-extended .carousel-control {
  width: 4%;
  background-image: none;
}

.carousel-extended .carousel-control.left {
  margin-left: 25px;
}

.carousel-extended .carousel-control.right {
  margin-right: 25px;
}

.carousel-extended .cloneditem-1,
.carousel-extended .cloneditem-2,
.carousel-extended .cloneditem-3,
.carousel-extended .cloneditem-4,
.carousel-extended .cloneditem-5 {
  display: none;
}

@media all and (min-width: 768px) and (transform-3d), all and (min-width: 768px) and (-webkit-transform-3d) {
  .carousel-extended .carousel-inner > .item.active.right,
  .carousel-extended .carousel-inner > .item.next {
    transform: translate3d(33.333%, 0, 0);
    left: 0;
  }
  .carousel-extended .carousel-inner > .item.active.left,
  .carousel-extended .carousel-inner > .item.prev {
    transform: translate3d(-33.333%, 0, 0);
    left: 0;
  }
  .carousel-extended .carousel-inner > .item.left,
  .carousel-extended .carousel-inner > .item.prev.right,
  .carousel-extended .carousel-inner > .item.active {
    transform: translate3d(0, 0, 0);
    left: 0;
  }
}

@media all and (min-width: 768px) {
  .carousel-extended .carousel-inner > .active.left,
  .carousel-extended .carousel-inner > .prev {
    left: -33.333%;
  }
  .carousel-extended .carousel-inner > .active.right,
  .carousel-extended .carousel-inner > .next {
    left: 33.333%;
  }
  .carousel-extended .carousel-inner > .left,
  .carousel-extended .carousel-inner > .prev.right,
  .carousel-extended .carousel-inner > .active {
    left: 0;
  }
  .carousel-extended .carousel-inner .cloneditem-1,
  .carousel-extended .carousel-inner .cloneditem-2 {
    display: block;
  }
}

@media all and (min-width: 992px) and (transform-3d), all and (min-width: 992px) and (-webkit-transform-3d) {
  .carousel-extended .carousel-inner > .item.active.right,
  .carousel-extended .carousel-inner > .item.next {
    transform: translate3d(16.666%, 0, 0);
    left: 0;
  }
  .carousel-extended .carousel-inner > .item.active.left,
  .carousel-extended .carousel-inner > .item.prev {
    transform: translate3d(-16.666%, 0, 0);
    left: 0;
  }
  .carousel-extended .carousel-inner > .item.left,
  .carousel-extended .carousel-inner > .item.prev.right,
  .carousel-extended .carousel-inner > .item.active {
    transform: translate3d(0, 0, 0);
    left: 0;
  }
}

@media all and (min-width: 992px) {
  .carousel-extended .carousel-inner > .active.left,
  .carousel-extended .carousel-inner > .prev {
    left: -16.666%;
  }
  .carousel-extended .carousel-inner > .active.right,
  .carousel-extended .carousel-inner > .next {
    left: 16.666%;
  }
  .carousel-extended .carousel-inner > .left,
  .carousel-extended .carousel-inner > .prev.right,
  .carousel-extended .carousel-inner > .active {
    left: 0;
  }
  .carousel-extended .carousel-inner .cloneditem-3,
  .carousel-extended .carousel-inner .cloneditem-4,
  .carousel-extended .carousel-inner .cloneditem-5 {
    display: block;
  }
}

@import url(https://fonts.googleapis.com/css?family=Bree+Serif|Titillium+Web);
body {
  background: url("/Images/movielarge_wp.jpg") no-repeat center border-box fixed;
  background-size: cover;
  padding-bottom: 60px;
}

#main {
  font-family: 'Titillium Web', sans-serif;
}

#main div.row {
  background: url("/Images/blackframe.jpg") repeat-x border-box;
  background-position: 0 0;
  background-size: contain;
  padding-top: 30px;
  height: auto;
}

#main #historywrapper .navbar.navbar-default {
  margin-bottom: 0;
  margin-left: -15px;
  margin-right: -15px;
  height: 70px;
  background-image: none;
  background: -moz-linear-gradient(top, rgba(36, 161, 151, 0.47) 0%, rgba(42, 160, 152, 0.47) 14%, rgba(46, 160, 153, 0.47) 24%, #429e9b 69%, #4f9d9d 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(top, rgba(36, 161, 151, 0.47) 0%, rgba(42, 160, 152, 0.47) 14%, rgba(46, 160, 153, 0.47) 24%, #429e9b 69%, #4f9d9d 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, rgba(36, 161, 151, 0.47) 0%, rgba(42, 160, 152, 0.47) 14%, rgba(46, 160, 153, 0.47) 24%, #429e9b 69%, #4f9d9d 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#7824a197', endColorstr='#4f9d9d',GradientType=0 );
  /* IE6-9 */
}

#main #historywrapper .navbar-brand {
  margin-left: 10px;
}

@media all and (max-width: 768px) {
  #main #historywrapper .navbar-header {
    width: 30%;
  }
}

@media all and (max-width: 768px) {
  #main #historywrapper div.nav.navbar-nav.navbar-right {
    width: 20%;
    position: absolute;
    left: 65%;
    top: -5px;
  }
}

@media all and (max-width: 768px) {
  #main #historywrapper ul.dropdown-menu {
    position: absolute;
    width: 300px;
    background-color: #fff;
    top: 65px;
    left: -156px;
  }
}

#main #historywrapper .navbar-text {
  color: #333333;
  padding-left: 15px;
  padding-top: 5px;
  font-size: 1.5em;
}

@media all and (max-width: 768px) {
  #main #historywrapper .navbar-text {
    display: none;
  }
}

#main #historywrapper .dropdown > button {
  margin: 15px 20px;
}

#main #Content {
  padding-top: 40px;
  font-family: 'Bree Serif', serif;
  font-size: 2em;
  color: #121212;
}

#main footer {
  background-image: none;
  background-color: #333333;
  font-family: 'Bree Serif', serif;
  font-size: 1.2em;
  color: #fff;
  text-align: center;
}

#main footer p:first-child {
  margin-bottom: -15px;
  padding-top: 10px;
  font-size: 1.4em;
}

@media all and (max-width: 768px) {
  #main footer p:first-child {
    margin-bottom: 0;
  }
}

#main footer p a {
  color: inherit;
  text-decoration: none;
  display: inline-block;
}

#dropHistoryContent {
  height: 300px;
  width: 300px;
  overflow-y: auto;
  background: #dadada;
}


